  border-radius: 80%;
  font-size: 2rem;
  line-height: 3rem;
  transition: all 0.7s;
}
.btn-border span::before {
  content: "";
  position: absolute;
  width: 9rem;
  height: 9rem;
  left: 0;
  top: 0;
  transition: all 0.4s;
  background-image: url(../img/icons/border-btn.svg);
