  transition: all 0.7s;
  margin-right: 2rem;
  background: #ed8b54;
  border-radius: 50%;
}
.btn-border:hover span::before {
  transition: all 0.4s;
  opacity: 0;
}
.btn-border:focus {
  transition: all 0.3s;
  color: #f28d55;
}
.btn-border:focus span {
  transition: all 0.7s;
  margin-right: 2rem;
  background: #ed8b54;
  border-radius: 50%;
  color: #ffffff;
}
.btn-border:focus span::before {
  transition: all 0.4s;
  opacity: 0;
}

.btn {
  position: relative;
  z-index: 0;
  transition: all 0.7s;
  border: 1px solid transparent;
}
.btn::after, .btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid transparent;
  width: 0;
  height: 0;
  z-index: -1;
  transition: all 0.7s;
}
.btn::before {
  width: 100%;
  height: 100%;
  background: #f28d55;
  opacity: 1;
}
.btn::after {
  border: 1px solid #f28d55;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.7s;
}
.btn:hover {
  transition: all 0.7s;
  box-shadow: 2px 4px 6px rgba(255, 227, 211, 0.54);
}
.btn:hover::after {
  background: linear-gradient(180deg, rgba(100, 17, 166, 0.2) 0.01%, rgba(63, 4, 109, 0.2) 100%);
  border: 1px solid #f28d55;
  border-radius: 0%;
  box-shadow: none;
  opacity: 1;
  transition: all 0.7s;
}
.btn:hover::before {
  transition: all 0.7s;
  opacity: 0;
}

