html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
  background: #000000;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Rubik-Regular", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
}

header,
section,
footer {
  width: 100%;
}

.wrapper {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

.container {
  width: 100%;
  max-width: 162rem;
  padding: 0 1.5rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (min-width: 1140px) {
    padding: 0 7rem;
  }
  @media (min-width: 1740px) {
    padding: 0 15rem;
  }
}
.containers {
  width: 100%;
  max-width: 162rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (min-width: 1140px) {
    padding: 0 7rem;
  }
  @media (min-width: 1740px) {
    padding: 0 15rem;
  }
}
.no-scroll {
  overflow: hidden;
}
